header {
  position: fixed;
  width: 100%;
  background-color: #479dff;
  z-index: 100;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
}
header a {
  text-decoration: none;
  transition: 0.3s;
}
header .header-wrap {
  height: 60px;
  display: flex;
  justify-content: center;
}
header .header-wrap .full-header {
  position: relative;
  display: flex;
  max-width: 1300px;
  width: 100%;
  padding: 0 10px;
  line-height: 60px;
}
header .header-wrap .full-header .full-nav {
  margin: 0 10px;
}
header .header-wrap .full-header .full-nav a {
  color: rgb(54, 54, 54);
  margin: 0 10px;
}
@media screen and (max-width: 600px) {
  header .header-wrap .full-header {
    justify-content: space-between;
  }
}
header .header-wrap .full-header a:hover {
  color: rgb(0, 0, 0);
}
@media screen and (max-width: 600px) {
  header .header-wrap {
    display: none;
  }
}

.small-header {
  display: none;
}
@media screen and (max-width: 600px) {
  .small-header {
    display: block;
  }
}

.burger-wrap {
  width: 100%;
  padding: 0 10px;
  line-height: 60px;
  background-color: #479dff;
}
.burger-wrap .burger-icon {
  margin-top: 12px;
  line-height: normal;
  float: right;
}
.burger-wrap .burger-icon button {
  background: none;
  border: solid rgb(42, 52, 83) 0.1px;
  border-radius: 5px;
  transition: 0.2s;
}
.burger-wrap button:active {
  background-color: #3d98ff;
}

.dropdown {
  -webkit-animation: dropdown 0.7s ease-in-out forwards;
          animation: dropdown 0.7s ease-in-out forwards;
  -webkit-transform-origin: top center;
          transform-origin: top center;
}
.dropdown ul {
  margin: 0;
  padding: 0;
}
.dropdown ul li {
  line-height: 50px;
  margin: 0 2px;
  padding: 0 10px;
  list-style: none;
  transition: 0.2s;
}
.dropdown ul li a {
  color: rgb(54, 54, 54);
}
.dropdown ul li:hover {
  background-color: aliceblue;
  border-radius: 3px;
}

.logo {
  font-family: "Unica One", cursive;
  font-size: 24px;
  color: black;
}

.logo:hover {
  color: black;
}

@-webkit-keyframes dropdown {
  0% {
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
  }
  70% {
    -webkit-transform: rotateX(20deg);
            transform: rotateX(20deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

@keyframes dropdown {
  0% {
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
  }
  70% {
    -webkit-transform: rotateX(20deg);
            transform: rotateX(20deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}/*# sourceMappingURL=header.css.map */
